
.input-group{display:-ms-flexbox;display:flex;width:100%;max-width:450px;margin-bottom:10px
}
.input-group__inner{-ms-flex:1;flex:1;padding:7px 8px;border:1px solid #ccc;border-right:0;background:#fff;font-size:13px;line-height:13px;outline:0
}
.input-group__append{display:inline-block;border:1px solid #ccc;background-color:#eee;background-image:linear-gradient(#fcfcfc, #eee)
}



